"use client"

import { useState, useEffect, useMemo, useCallback } from "react"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Textarea } from "@/components/ui/textarea" 
import { 
  Star, Loader2, Trash2, Crown, Trophy, Heart, Zap, Quote, BookOpen, Layers, BarChart3, ChevronDown, Edit3
} from "lucide-react"
import { getReadingData } from "@/lib/api-client"

const GENRE_CATEGORIES = {
  "Ficção e Narrativa": ["Romance", "Dark Romance", "Romance de Época", "Chick-Lit", "Fantasia", "Ficção Científica", "Distopia", "Terror / Horror", "Suspense / Thriller", "Policial / Noir", "Aventura", "Young Adult (YA)", "Conto / Novela"],
  "Não-Ficção": ["Biografia / Memórias", "Matemática", "Autoajuda", "Filosofia / Sociologia", "Didático / Técnico", "Religioso / Espiritual", "Gastronomia"],
  "Clássicos e Formatos": ["Poesia / Lírico", "Dramático (Teatro)", "Graphic Novel", "Outro"]
};

const PLACEHOLDER_IMAGE = "https://images.unsplash.com/photo-1543002588-bfa74002ed7e?q=80&w=300&auto=format&fit=crop";

export function MonthReview({ month, userEmail, monthIndex, year }: any) {
  const [allBooks, setAllBooks] = useState<any[]>([])
  const [loading, setLoading] = useState(true)
  const [bookEdits, setBookEdits] = useState<Record<string, any>>({})
  const [isSaving, setIsSaving] = useState<string | null>(null)
  const [isDeleting, setIsDeleting] = useState<string | null>(null)
  const [isSearching, setIsSearching] = useState<string | null>(null);

  const loadData = useCallback(async () => {
    if (!userEmail || !year) return
    try {
      setLoading(true)
      const data: any = await getReadingData(userEmail, year)
      const booksArray = Array.isArray(data) ? data : (data?.data || [])
      setAllBooks(booksArray)

      const initialEdits: any = {}
      booksArray.forEach((b: any) => {
        initialEdits[b.id] = {
          name: b.book_name || "",
          cover: b.cover_url || "",
          rating: Number(b.rating) || 0,
          pages: Number(b.total_pages) || 0,
          review: b.review || "",
          genre: b.genre || "" 
        }
      })
      setBookEdits(initialEdits)
    } catch (err) { console.error(err) } finally { setLoading(false) }
  }, [userEmail, year])

  useEffect(() => { loadData() }, [loadData, monthIndex])

  const stats = useMemo(() => {
    const currentMonthBooks = allBooks.filter(b => Number(b.month) === (monthIndex + 1));
    const finishedMonth = currentMonthBooks.filter(b => b.status !== 'lendo');
    const monthPages = finishedMonth.reduce((acc, b) => acc + (Number(bookEdits[b.id]?.pages) || Number(b.total_pages) || 0), 0);
    const biggest = [...currentMonthBooks].sort((a, b) => (Number(bookEdits[b.id]?.pages || b.total_pages) || 0) - (Number(bookEdits[a.id]?.pages || a.total_pages) || 0))[0];
    return { monthTotal: finishedMonth.length, monthPages, biggest, favorites: currentMonthBooks.filter(b => Number(bookEdits[b.id]?.rating || b.rating) === 5) }
  }, [allBooks, monthIndex, bookEdits])

  const handleSave = async (bookId: string, originalName: string) => {
    setIsSaving(bookId)
    try {
      const data = bookEdits[bookId]
      await fetch("/api/reading-data", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          action: "UPDATE_REVIEW", email: userEmail,
          oldBookName: originalName,
          bookName: data.name,
          rating: Number(data.rating), coverUrl: data.cover,
          totalPages: Number(data.pages), review: data.review,
          genre: data.genre, year: Number(year), month: monthIndex + 1
        }),
      })
      await loadData()
      alert("Salvo!")
    } catch (err) { alert("Erro ao salvar") } finally { setIsSaving(null) }
  }

  const handleDelete = async (bookId: string, bookName: string) => {
    if (!confirm(`Excluir "${bookName}"?`)) return
    setIsDeleting(bookId)
    try { await fetch(`/api/books/${bookId}`, { method: "DELETE" }); await loadData() } finally { setIsDeleting(null) }
  }

  if (loading) return <div className="flex justify-center p-10"><Loader2 className="animate-spin text-primary" /></div>

  return (
    <div className="space-y-8 pb-20 max-w-6xl mx-auto px-4">
      {/* Resumo Simplificado */}
      <Card className="p-8 bg-white rounded-[30px] shadow-sm grid grid-cols-2 gap-4">
        <div className="text-center border-r">
          <p className="text-5xl font-black">{stats.monthTotal}</p>
          <p className="text-[10px] uppercase font-bold text-slate-400">Livros no Mês</p>
        </div>
        <div className="text-center">
          <p className="text-5xl font-black">{stats.monthPages}</p>
          <p className="text-[10px] uppercase font-bold text-slate-400">Páginas no Mês</p>
        </div>
      </Card>

      {/* Lista de Livros */}
      <div className="grid grid-cols-1 gap-6">
        {allBooks.filter(b => Number(b.month) === (monthIndex + 1)).map((book) => (
          <Card key={book.id} className="flex flex-col md:flex-row overflow-hidden rounded-[30px] border-none shadow-lg">
            <div className="w-full md:w-48 h-64 md:h-auto bg-slate-100 relative">
              <img src={bookEdits[book.id]?.cover || PLACEHOLDER_IMAGE} className="w-full h-full object-cover" />
            </div>
            <div className="flex-1 p-6 space-y-4">
              <div className="flex justify-between items-start">
                <div className="w-full">
                  <p className="text-[8px] font-black uppercase text-slate-400">Nome do Livro</p>
                  <Input 
                    className="font-black text-xl uppercase italic border-none bg-slate-50" 
                    value={bookEdits[book.id]?.name || ""} 
                    onChange={(e) => setBookEdits(p => ({...p, [book.id]: {...p[book.id], name: e.target.value}}))}
                  />
                </div>
                <Button variant="ghost" size="icon" onClick={() => handleDelete(book.id, book.book_name)} className="text-slate-300 hover:text-red-500">
                  {isDeleting === book.id ? <Loader2 className="animate-spin" size={16}/> : <Trash2 size={18}/>}
                </Button>
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div>
                  <p className="text-[9px] font-bold uppercase text-slate-400">Capa (URL)</p>
                  <Input className="text-xs bg-slate-50" value={bookEdits[book.id]?.cover || ""} onChange={(e) => setBookEdits(p => ({...p, [book.id]: {...p[book.id], cover: e.target.value}}))} />
                </div>
                <div>
                  <p className="text-[9px] font-bold uppercase text-slate-400">Páginas</p>
                  <Input type="number" className="bg-slate-50" value={bookEdits[book.id]?.pages || ""} onChange={(e) => setBookEdits(p => ({...p, [book.id]: {...p[book.id], pages: e.target.value}}))} />
                </div>
              </div>

              <div className="flex flex-col md:flex-row gap-4 items-end justify-between">
                <div className="flex gap-1">
                  {[1,2,3,4,5].map(s => (
                    <Star key={s} size={24} className={`cursor-pointer ${s <= (bookEdits[book.id]?.rating || 0) ? "fill-amber-400 text-amber-400" : "text-slate-200"}`} onClick={() => setBookEdits(p => ({...p, [book.id]: {...p[book.id], rating: s}}))} />
                  ))}
                </div>
                <Button 
                  className="rounded-xl font-black uppercase text-[10px] tracking-widest px-10 h-12" 
                  onClick={() => handleSave(book.id, book.book_name)}
                  disabled={isSaving === book.id}
                >
                  {isSaving === book.id ? "Salvando..." : "Salvar Alterações"}
                </Button>
              </div>
            </div>
          </Card>
        ))}
      </div>
    </div>
  )
}